@font-face {
  font-family: "Roboto Condensed", sans-serif;
  src: url("../fonts/roboto-condensed-v18-cyrillic_latin-regular.woff") format("woff"), url("../fonts/roboto-condensed-v18-cyrillic_latin-regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed", sans-serif;
  src: url("../fonts/roboto-condensed-v18-cyrillic_latin-700.woff") format("woff"), url("../fonts/roboto-condensed-v18-cyrillic_latin-700.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "PT Mono", monospace;
  src: url("../fonts/pt-mono-v7-cyrillic-ext_latin_cyrillic-regular.woff") format("woff"), url("../fonts/pt-mono-v7-cyrillic-ext_latin_cyrillic-regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

body {
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
  font-size: 14px;
  line-height: 24px;
  font-family: "PT Mono", monospace;
  font-style: normal;
  font-display: swap; }

img {
  max-width: 100%;
  height: auto; }

picture {
  line-height: 0; }

.visually-hidden:not(:focus):not(:active),
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  min-height: 878px;
  padding-top: 33px;
  background: #1b2d37 url("../img/header_bg.png") no-repeat 50% 50%;
  background-size: cover; }

.page-header__wrapper {
  display: flex;
  max-width: 1420px;
  margin: 0 auto;
  min-height: 52px; }

.page-header__logo {
  margin-right: 117px;
  margin-left: 2px;
  margin-top: 4px; }
  .page-header__logo svg:first-child {
    padding-right: 9px; }
  .page-header__logo svg:nth-child(2) {
    margin-top: 6px; }

.page-header__contacts {
  display: flex;
  list-style: none;
  margin-left: auto;
  margin-right: 5px;
  font-family: "PT Mono", monospace;
  font-size: 16px;
  line-height: 18px;
  color: white; }

.page-header__contacts li {
  display: inline-block;
  position: relative; }
  .page-header__contacts li a {
    text-decoration: none;
    color: white; }
  .page-header__contacts li:first-child {
    margin-top: 20px;
    margin-right: 90px; }
    .page-header__contacts li:first-child svg {
      fill: #00b2ff;
      padding-top: 2px;
      padding-right: 5px; }
  .page-header__contacts li:nth-child(2) {
    margin-top: 17px;
    margin-right: 50px; }
    .page-header__contacts li:nth-child(2) svg {
      fill: #00b2ff;
      padding-top: 2px;
      padding-right: 5px; }

@media (max-width: 1023px) {
  .page-header {
    min-height: 687px; }
  .page-header__contacts li span,
  .page-header__contacts li:last-child {
    display: none; } }

@media (max-width: 767px) {
  .page-header {
    min-height: 647px; } }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.main-nav__toggle {
  position: relative;
  width: 37px;
  height: 10px;
  margin: 25px 20px 19px 0;
  padding: 0;
  border: none;
  background-color: transparent;
  /*background: url("../img/icon-menu-cross.svg") no-repeat; */
  outline: none;
  order: 2; }

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 37px;
  height: 1px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff;
  outline: none; }

.main-nav--closed .main-nav__toggle:hover::before,
.main-nav--closed .main-nav__toggle:focus::before {
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0 10px 0 0 rgba(255, 255, 255, 0.5); }

.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0 10px 0 0 rgba(255, 255, 255, 0.5), 0 20px 0 0 rgba(255, 255, 255, 0.5); }

.button {
  width: 180px;
  padding: 14px 35px 17px 35px;
  font: inherit;
  font-size: 14px;
  line-height: 16.4px;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  border-radius: 5px; }
  .button:hover {
    font-weight: 400;
    color: rgba(255, 255, 255, 0.5); }
  .button:active {
    font-weight: 400;
    color: rgba(255, 255, 255, 0.5); }
  .button--callback {
    width: 180px;
    padding: 12px 24px;
    margin-top: 10px;
    color: #ffffff;
    background-color: transparent;
    border: 1px solid #ffffff; }
    .button--callback:hover, .button--callback:active {
      border: 1px solid rgba(255, 255, 255, 0.5); }
